.app .minicard.compact .minicard-add-button,
.app .minicard.compact .minicard-button-icon,
.app .minicard.compact .minicard-button-thumb {
  height: calc(var(--accessory-button-height) * 0.75);
}

.app .minicard-add-button {
  margin-bottom: 5px;
  border: 1px solid var(--grey-1x);
  box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.07);
  display: flex;
  align-items: center;
  height: var(--accessory-button-height);
  box-sizing: border-box;
}
.app .minicard-add-button:hover {
  border: 1px solid var(--grey-2);
  box-shadow: 1px 4px 6px rgba(0, 0, 0, 0.15);
}
.app .minicard-add-button:hover .minicard-button-add-icon path {
  fill: var(--accent-dark);
}

.app .minicard-button-icon {
  height: var(--accessory-button-height);
  width: var(--accessory-button-height);
  display: flex;
  justify-content: center;
  align-items: center;
  border-left: 1px solid var(--grey-1x);
  cursor: pointer;
  font-size: 22px;
  color: var(--grey-3);
  flex-shrink: 0;
}
.app .minicard-button-icon:hover {
  color: var(--accent-dark);
  background-color: var(--grey-1);
}
/* .app .minicard-button-icon-added {
  background-color: var(--confirm-bg);
} */
.app .minicard-button-icon-added .minicard-button-icon-check {
  display: flex;
  color: var(--confirm);
}

.app .minicard-button-icon-added .minicard-button-icon-remove {
  display: none;
  color: var(--warning);
}
/* .minicard-button-icon-added:hover .minicard-button-icon-remove {
    display: flex;
  } */
/* .minicard-button-icon-added:hover .minicard-button-icon-check {
    display: none;
  } */

.app .minicard-button-thumb {
  width: var(--accessory-button-height);
  height: var(--accessory-button-height);
  /* margin-left: 4px; */
  flex-shrink: 0;
  cursor: pointer;
}
.app .minicard-button-thumb img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.app .minicard-button-label {
  flex-grow: 1;
  margin-left: 10px;
  cursor: pointer;
}

.app .minicard-button-title {
  font-size: 15px;
  font-weight: 300;
  line-height: 16px;
  display: block;
}

.app .minicard-button-subtitle {
  font-size: 14px;
  line-height: 18px;
  color: var(--grey-3);
  display: block;
  text-decoration: none;
  align-self: center;
}

.app .minicard-button-info-wrapper {
  flex-grow: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
